<template>
<v-container>
    <v-row>
        <v-col cols="12">
            <v-card >
          <v-card-text>
            <h3 class="title blue-grey--text text--darken-2 font-weight-regular">{{'Geri Bildirim Gönderme Sayfası'}}</h3>
            <h6 class="subtitle-2 font-weight-light">{{'Hata, fikir ve önerilerinizi bize gönderebilirsiniz.'}}</h6>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-text>
            <v-text-field
              :error-messages="errName"
              v-model="feedback.title"
              :label="'Başlık'"
              filled
              background-color="transparent"
            ></v-text-field>
             <v-textarea
             counter="140"
             v-model="feedback.content"
            :error-messages="errContent"
            name="input-7-1 w-100"
            :label="'Açıklama'"
            hint="Sorun veya önerinin açıklamasını giriniz"
        ></v-textarea>
         <v-col cols="12">
        <el-upload
  class="upload-demo"
  drag
  :action="'#'"
  :on-change="fileUpload"
  :multiple="false"
  :limit="1"
  :auto-upload="false">
  <i class="el-icon-upload"></i>
  <div class="el-upload__text">Drop file here or <em>click to upload</em></div>
  <div class="el-upload__tip" slot="tip">jpg/png/pdf dosyalarınu yükleyebilirsiniz ve yüklenilecek dosya 2MB'dan küçük olmalıdır.</div>
</el-upload>
<p class="text-danger">{{errMsg}}</p>
    </v-col>
            <v-btn  :loading="loading" :disabled="loading" class="text-capitalize mt-5 element-0" @click="sendFeedBack()" color="success">{{$t('generate.submit')}}</v-btn>
          </v-card-text>
        </v-card>
        </v-col>
    </v-row>
    <snackBar :snackbar="snackbar" @finished="snackbar=$event" />
</v-container>
</template>

<script>
export default {
data(){
    return{
        errName:[],
        errContent:[],
        feedback:{
            title:"",
            content:"",
            file:{uid:-1}
        },
        loading:false,
        errMsg:""
    }
},
methods:{
    fileUpload(e){
      if(e.raw.type==' application/pdf ' || e.raw.type=='image/jpeg' || e.raw.type=='image/png'){
          if(e.size>2048000){
       this.errMsg = "Dosya Boyutunuz 5MB üzerinde lütfen " +e.raw.type +' dosyanızı kaldırın. Dosya Boyutu : ' +e.raw.size+' bayt'
      }
      else{
        this.errMsg=""
       this.feedback.file = e.raw;
      }
      }
      else{
          this.errMsg="Dosya formatınız .pdf, .jpg, .jpeg veya .png olmalıdır."
      }
     
    },
    sendFeedBack(){
        this.errName=[]
        this.errContent=[]
        if(this.feedback.title==''||this.feedback.content==''||this.feedback.file.uid==-1||this.errMsg!=''){
            if(this.feedback.title==''){
                this.errName.push('Geri bildirim başlığı olmak zorundadır.')
            }
            if(this.feedback.content==''){
                this.errContent.push('Geri bildirim içeriği olmak zorundadır.')
            }
             if(this.feedback.file.uid==-1){
                this.errMsg = "Dosya Seçiniz."
            }
        }
        else{
            let fd = new FormData()
            fd.append('title',this.feedback.title)
            fd.append('image',this.feedback.file)
            fd.append('content',this.feedback.content)
            this.$api.postfd('feedback','file',fd).then(res=>{
              if(res.status=='success'){
                  this.snackbar=true
                  this.feedback={
                       title:"",
                 content:"",
                  file:{uid:-1}
                  }
              }
                
            })
        }
    }
}
}
</script>

<style>
.el-upload, .el-upload-dragger {
    width: 100% !important;
}
</style>